<template>
    <nav>
        <v-app-bar dark flat app color="#0f111c">
            <template>
                <v-tabs v-if="$vuetify.breakpoint.mdAndUp" v-model = "item" slider-size="4" centered >
                    <v-tab v-for="item in items" :key="item" @click="$vuetify.goTo('#'+ item, options)" :href = "'#'+ item">
                        {{ item }}
                    </v-tab>
                    <v-tabs-items>
                        <v-tab-item v-for="item in items" :key="item" value = '#' + item>
                        </v-tab-item>
                    </v-tabs-items>
                    <v-tabs-slider></v-tabs-slider>
                </v-tabs>
                <v-app-bar-nav-icon v-if="$vuetify.breakpoint.smAndDown" @click="sideNav = true"></v-app-bar-nav-icon>
            </template>
        </v-app-bar>
        <v-navigation-drawer v-model="sideNav" app>
            <v-list>
                <v-list-item v-for="item in items" :key="item"
                    @click="$vuetify.goTo('#' + item, options); sideNav = false;">
                    <v-list-item-content>
                        <v-list-item-title>
                            {{ item }}
                        </v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>
    </nav>
</template>




<script>


export default{    
    data() {
        return {
            item:null,
            sideNav:false
        }
    },
    props:{
        entries:{
            require: true,
            default: "home"
        },
        items:{}
    },
    computed:{
        options (){
            return {
                duration : 30,
                offset : 3,
                easing: 'linear'
            }
        },
    },
    watch: {
        entries: function () { // watch it
        if (this.$props.entries == "home") {
            this.item = "home"
        }
        else if (this.$props.entries == "about") {
            this.item = "about"
        }
        else if (this.$props.entries == "project"){
            this.item = "project"
        }
        else if (this.$props.entries == "award") {
            this.item = "award"
        }
        else if (this.$props.entries == "contact") {
            this.item = "contact"
        }
    }
    },
    methods: {
        // eslint-disable-next-line no-unused-vars
    
        
    }
    
}

</script>

<style scoped>
.v-tab{
    color: #fefefe !important;
}
.v-tab:active {
    color: black !important;
    background-color:#62E570 !important;
}
.v-tabs-slider {
    color: #62E570 !important;
}
.v-navigation-drawer{
    background-color: #0f111c;
    padding-top: 2vw;
    max-width: 35%;
    max-height: 40%;
}

.v-list-item__content {
    color:#fefefe;
    padding-left: 3vw;
}

.v-list-item__title{
    text-transform: capitalize;
}
element.style {
    height: none !important;
    top: 0px;
    transform: translateX(0%);
    width: 256px;
}
</style>
